import './App.css';
import React, {useState} from 'react';
import Tours from './Tours';
import DB from './db'

function App() {
  
  const [tours, setTours] = useState(DB);
  
  const removeTour = (id) => {
    const newTour = tours.filter((tour)=>tour.id !== id);
    setTours(newTour)
  }
  
    function refreshPage() {
      window.location.reload(false);
    }
  
  if(tours.length === 0){
    return (
      <main>
        <div className="title">
        <h2>No Tours Left</h2>
        <button className="refresh" onClick={()=>refreshPage()}>Refrish</button>
        </div>
      </main>
    )
  }
    return (
      <main>
        <Tours tours={tours} removeTour={removeTour} />
      </main>
    );
  }
 

export default App;
